import { Component, h, Prop, EventEmitter, Event, State } from '@stencil/core';

/**
 * @category Form Controls
 * @description A search component for filtering data.
 */

@Component({
    tag: 'at-search',
    shadow: false,
})
export class AtSearch {
    /**
     * Label text above the search bar.
     */
    @Prop()
    label?: string;

    /**
     * Hint text for the input, appears below the search bar.
     */
    @Prop()
    hint_text?: string;

    /**
     * Tooltip description.
     */
    @Prop()
    info_text?: string;

    /**
     * Placeholder text inside the search component.
     */
    @Prop()
    placeholder?: string;

    /**
     * String content of the search.
     */
    @Prop({ mutable: true })
    model?: string;

    @State()
    searchEl: HTMLInputElement;

    /**
     * Emits an event when the input is changed. Used by atui-table.
     */
    @Event({ eventName: 'atChange' })
    atuiChange: EventEmitter<string>;

    onChangeFn(): void {
        this.atuiChange.emit(this.searchEl.value);
        this.model = this.searchEl.value;
    }

    clearFn(): void {
        this.searchEl.value = '';
        this.atuiChange.emit(this.searchEl.value);
        this.model = '';
        this.searchEl.focus();
    }

    render() {
        return (
            <div>
                {(this.label || this.info_text) && (
                    <at-form-label
                        label={this.label}
                        for={
                            this.label
                                ? `search-input-${this.label.replace(/\s+/g, '-').toLowerCase()}`
                                : undefined
                        }
                        info_text={this.info_text}
                    />
                )}
                <div class="active-within:border-active-dark transition[background-color,color,box-shadow] border-med bg-surface-0 focus-within:border-active-dark focus-within:ring-active-foreground/30 relative flex h-[36px] flex-row items-center justify-center rounded-md border duration-300 ease-in-out focus-within:ring-2">
                    <span class="px-8">
                        <i class="material-icons text-light !text-[16px] !leading-[30px]">
                            search
                        </i>
                    </span>

                    <input
                        id={
                            this.label
                                ? `search-input-${this.label.replace(/\s+/g, '-').toLowerCase()}`
                                : undefined
                        }
                        role="searchbox"
                        tabindex="0"
                        class="mr-24 h-30 w-full bg-transparent p-0 leading-[30px] focus:border-none focus:outline-none"
                        placeholder={this.placeholder}
                        value={this.model}
                        autocomplete="off"
                        name="search"
                        onInput={() => this.onChangeFn()}
                        ref={(el) => (this.searchEl = el)}
                    />

                    <div
                        class={`transition-all duration-300 ease-in-out ${
                            this.model !== '' && this.model !== undefined
                                ? 'pr-4 opacity-100'
                                : 'pointer-events-none pr-0 opacity-0'
                        }`}
                    >
                        <at-button
                            size="sm"
                            type="secondaryText"
                            icon="backspace"
                            onClick={() => this.clearFn()}
                            data-name="search-clear"
                            tabindex={
                                this.model !== '' && this.model !== undefined
                                    ? '0'
                                    : '-1'
                            }
                        ></at-button>
                    </div>
                </div>

                <span
                    class="text-med mt-4 truncate text-xs !leading-normal font-normal"
                    data-name="search-hint"
                >
                    {this.hint_text}
                </span>
            </div>
        );
    }
}
